import React, { useState, useEffect } from 'react';
import { initMainButton } from '@telegram-apps/sdk';

type MainButtonType = {
  show: () => void;
  // Add other properties/methods of MainButton here if needed
};

export default function SDKReact() {
  // Initialize state with `MainButtonType | null` to handle both cases
  const [mainButton, setMainButton] = useState<MainButtonType | null>(null);

  useEffect(() => {
    const initializeButton = async () => {
      try {
        const [button] = await initMainButton();
        setMainButton(button); // This should now match the type
        button.show(); // Show button after it has been initialized
      } catch (error) {
        console.error('Error initializing main button:', error);
      }
    };

    initializeButton();
  }, []);

  if (!mainButton) {
    return <div>Loading...</div>; // Show loading state until mainButton is initialized
  }

  return (
    <div className="w-full text-white h-full font-bold flex flex-col max-w-xl">
      <div className="relative min-h-screen loading-screen pb-7">
        <h1>SDK React</h1>
      </div>
    </div>
  );
}
